                <tr>
                    <th>Username {{msg}}</th>
                    <th>Email</th>
                    <th>Full Name</th>
                    <th>Roles</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td> user.username </td>
                    <td> user.email </td>
                    <td> user.full_name </td>
                    <td> user.display_roles </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
export default {
    data () {
        return {
            msg: 'Hello'
        }
    }
}
</script>

